// const api_key = "client_id=a7Ucuq0KY8Ksn8WzBG6wj4x6pcId6BpU"
const api_key_dev = "client_id=JAgPhXap7XK0g8dUOtklbE7UnF05W8AH"
// const api_key_prod = "client_id=x3d1i5dxXwTtUNJAy8djMDh7yYdxSZX0"

const tracks = `https://api.soundcloud.com/tracks?linked_partitioning=1&limit=40&offset=0&${api_key_dev}`

export const genreUrl = genre => `${tracks}&genres=${genre}`

export const audioStream = songStream => `${songStream}?${api_key_dev}`

export const searchUrl = q => {
  const query = q
    .split(" ")
    .filter(str => str.length > 0)
    .join("%20")
  return `${tracks}&q=${query}`
}



